<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  data() {
    return {
      selected: 'Buy',
      email: '',
      modal: false,
      states: 'searchData',
      details: {},
      options: [
        { value: 'Buy', text: 'Buy' },
        { value: 'Sell', text: 'Sell' },
      ],
      errorfind: false,
    };
  },
  watch: {
    selected(newValue, oldValue) {
      // This method will be triggered when the selected option changes
      console.log('Selected option changed from', oldValue, 'to', newValue);
      if (newValue == 'Buy') {
        this.getListBuy();
      }
      if (newValue == 'Sell') {
        this.getListSell();
      }
      // You can call your method here or perform any other action
      // this.yourMethod(newValue);
    },
  },
  computed: {
    ...mapGetters('airdroppendindgStore', ['pageData', 'listData', 'stateData']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
  },
  methods: {
    ...mapActions('airdroppendindgStore', ['getList', 'getPrevData', 'getNextData', 'approveAirdrop', 'rejectAirdrop']),
    async prevPage() {
      await this.getPrevData();
    },
    async nextPage() {
      await this.getNextData();
    },
    async approve(_id) {
      await this.approveAirdrop(_id);
      await this.getList('');
    },
    async reject(_id) {
      await this.rejectAirdrop(_id);
      await this.getList('');
    },
  },
  async mounted() {
    await this.getList('');
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
